body {
  background-color: rgb(31, 30, 30);
  text-align: center;
}

img {
  padding: 2rem;
  width: 37vw;
}

button {
  height: 2rem;
  width: 25vw;
}

.main {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 500px) {
  button {
    width: 50vw;
  }

  img {
    width: 85vw;
  }
}

@media screen and (max-width: 778px) {
  img {
    width: 50vw;
  }
}